<template>
  <v-container fluid>
    <v-app-bar app class="indigo" dark>
      <v-card-title class="headline">
        <v-btn icon @click="goBack">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        <img src="/favicon.ico" alt="Logo" class="logo-small">
        Dovepin Dashboard
      </v-card-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-container>
      <v-row>
        <v-col cols="12" sm="4">
          <v-card class="menu-card">
            <v-card-title class="headline">Menu View Karyawan</v-card-title>
            <v-card-text>
              <p>Menu untuk melihat data karyawan</p>
              <v-btn color="primary" @click="goToListKaryawan">Lihat Data Karyawan</v-btn>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12" sm="4">
          <v-card class="menu-card">
            <v-card-title class="headline">Menu View Absen</v-card-title>
            <v-card-text>
              <p>Menu untuk melihat data absen</p>
              <v-btn color="primary" @click="goToListAbsen">Lihat Data Absen</v-btn>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12" sm="4">
          <v-card class="menu-card">
            <v-card-title class="headline">Menu View Cuti</v-card-title>
            <v-card-text>
              <p>Menu untuk melihat data cuti</p>
              <v-btn color="primary" @click="goToListCuti">Lihat Data Cuti</v-btn>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';
import Swal from 'sweetalert2';

export default {
  name: 'DashboardView',
  computed: {
    ...mapGetters(['getUserRole']),
  },
  metaInfo() {
    return {
      meta: [
        {
          name: 'requiresAuth', // Nama meta field
          content: true, // Nilai meta field, bisa diganti dengan kondisi sesuai kebutuhan
        },
        {
          name: 'requiredRole', // Nama meta field
          content: 'admin', // Nilai meta field, bisa diganti dengan kondisi sesuai kebutuhan
        },
      ],
    };
  },
  methods: {
    checkUserRole() {
      const userRole = this.getUserRole();
      if (userRole !== 'admin') {
        this.$router.push('/home');
        Swal.fire({
          icon: 'warning',
          title: 'Akses Ditolak',
          text: 'Anda tidak memiliki akses ke halaman ini.',
          confirmButtonText: 'OK',
        });
      }
    },

    goBack() {
      this.$router.push('/home');
    },
    goToListKaryawan() {
      this.$router.push('/list-karyawan');
    },
    goToListAbsen() {
      this.$router.push('/list-absen');
    },
    goToListCuti() {
      this.$router.push('/list-cuti');
    },
  },
};
</script>

<style scoped>
.logo-small {
  width: 50px;
  height: 50px;
  display: block;
  margin: 0 auto;
}

.menu-card {
  cursor: pointer;
  text-align: center;
  padding: 16px;
  margin-bottom: 16px;
}

.headline {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
}
</style>
