import Vue from "vue";
import VueRouter from "vue-router";
import AppLayout from "@src/layouts/AppLayout.vue";
import Home from "@src/views/HomeView.vue";
import Login from "@/views/auth/LoginView.vue";
import Register from "@/views/auth/RegisterView.vue";
import Karyawan from "@/views/employee/KaryawanView.vue";
import Cuti from "@/views/employee/CutiView.vue";
import CheckIn from "@/views/attendance/CheckIn.vue";
import CheckOut from "@/views/attendance/CheckOut.vue";
import Dashboard from "@/views/DashboardView.vue";
import ListKaryawan from "@/views/listdashboard/ListKaryawanView.vue";
import ListAbsen from "@/views/listdashboard/ListAbsenView.vue";
import ListCuti from "@/views/listdashboard/ListCutiView.vue";
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';


delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
});

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: AppLayout,
    children: [
      {
        path: "",
        redirect: "/login" // Redirect to login page by default
      },
      {
        path: "/login",
        component: Login
      },
      {
        path: "/register",
        component: Register
      },
      {
        path: "/home",
        component: Home
      },
      {
        path: '/dashboard',
        component: Dashboard,
        meta: { userRole: 'admin' },
      },
      {
        path: "/list-karyawan",
        component: ListKaryawan
      },
      {
        path: "/list-absen",
        component: ListAbsen
      },
      {
        path: "/list-cuti",
        component: ListCuti
      },
      {
        path: "/karyawan",
        component: Karyawan
      },
      {
        path: "/cuti",
        component: Cuti
      },
      {
        path: "/checkin",
        component: CheckIn
      },
      {
        path: "/checkout",
        component: CheckOut
      },           
    ]
  }
];

const router = new VueRouter({
  routes
});

router.beforeEach((to, from, next) => {
  const isLoggedIn = localStorage.getItem("isLoggedIn");
  const userRole = localStorage.getItem("userRole");
  
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // Periksa jika pengguna belum login
    if (!isLoggedIn) {
      next("/login");
    } else {
      // Periksa jika pengguna bukan admin
      if (userRole !== "admin") {
        next("/home");
      } else {
        next();
      }
    }
  } else {
    next();
  }
});

export default router;
