<template>
  <v-container fluid>
    <v-app-bar app class="indigo" dark>
      <v-card-title class="headline">
        <v-btn icon @click="goBack">
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        <img src="/favicon.ico" alt="Logo" class="logo-small">
        Dovepin
      </v-card-title>
      <v-spacer></v-spacer>
    </v-app-bar>

    <v-container class="content-container">
      <v-card class="profile-card">
        <v-card-text>
          <h1>{{ karyawanData.displayName }}</h1>
          <table class="profile-table">
            <tr>
              <td>Jenis Kelamin: {{ karyawanData.jenisKelamin }}</td>
            </tr>
            <tr>
              <td>Jabatan: {{ karyawanData.jabatan }}</td>
            </tr>
          </table>
        </v-card-text>
      </v-card>

      <v-card class="cuti-card">
        <v-card-title class="headline">Permintaan Cuti</v-card-title>
        <v-card-text>
          <v-form @submit.prevent="submitCutiForm">
            <v-row>
              <v-col cols="12" sm="6">
                <v-text-field v-model="tanggalCuti" label="Tanggal Cuti" type="date"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field v-model="lamaCuti" label="Lama Cuti (hari)" type="number"></v-text-field>
              </v-col>
            </v-row>
            <v-textarea v-model="keterangan" label="Keterangan" rows="3" auto-grow></v-textarea>
            <v-select v-model="statusCuti" label="Status Cuti" :items="['Menunggu']"></v-select>
            <div class="submit-container">
              <v-btn type="submit" color="primary">Submit</v-btn>
            </div>
          </v-form>
        </v-card-text>
      </v-card>
    </v-container>
  </v-container>
</template>

<script>
import firebase from "firebase/app";
import "firebase/auth";
import { db } from "@/main";
import Swal from "sweetalert2";

export default {
  name: "CutiView",
  data() {
    return {
      karyawanData: {
        displayName: "",
        jenisKelamin: "",
        jabatan: "",
      },
      tanggalCuti: null,
      lamaCuti: null,
      keterangan: "",
      statusCuti: "Menunggu",
    };
  },
  created() {
    const user = firebase.auth().currentUser;
    if (user) {
      this.getKaryawanData(user.email);
    } else {
      console.error("User not logged in.");
      this.$router.push("/home");
    }
  },
  methods: {
    async getKaryawanData(email) {
      try {
        const querySnapshot = await db
          .collection("registrations")
          .where("email", "==", email)
          .get();

        if (!querySnapshot.empty) {
          const registrationData = querySnapshot.docs[0].data();

          this.karyawanData = {
            displayName: registrationData.displayName,
            jenisKelamin: registrationData.jenisKelamin,
            jabatan: registrationData.jabatan,
            statusCuti: "Menunggu",
          };
        } else {
          Swal.fire({
            icon: "error",
            title: "Error",
            text: "Data pendaftaran karyawan tidak ditemukan",
          });
        }
      } catch (error) {
        console.error("Error getting karyawan data:", error);
        Swal.fire({
          icon: "error",
          title: "Error",
          text: "Terjadi kesalahan saat mengambil data karyawan",
        });
      }
    },
    submitCutiForm() {
      const user = firebase.auth().currentUser;

      if (user) {
        const cutiData = {
          displayName: this.karyawanData.displayName,
          jenisKelamin: this.karyawanData.jenisKelamin,
          jabatan: this.karyawanData.jabatan,
          tanggalCuti: this.tanggalCuti,
          lamaCuti: this.lamaCuti,
          keterangan: this.keterangan,
          statusCuti: this.karyawanData.statusCuti,
        };

        db.collection("cutis")
          .add(cutiData)
          .then(() => {
            Swal.fire({
              icon: "success",
              title: "Sukses",
              text: "Permintaan Cuti Berhasil Dikirim",
            });

            this.tanggalCuti = null;
            this.lamaCuti = null;
            this.keterangan = "";
          })
          .catch((error) => {
            Swal.fire({
              icon: "error",
              title: "Error",
              text: "Terjadi kesalahan saat mengirim permintaan cuti",
            });

            console.error("Error submitting cuti:", error);
          });
      } else {
        console.error("User not logged in.");
        this.$router.push("/home");
      }
    },
    goBack() {
      this.$router.push("/home");
    },
  },
};
</script>

<style scoped>
.logo-small {
  width: 50px;
  height: 50px;
  display: block;
  margin: 0 auto;
}

.content-container {
  padding: 24px;
}

.content-container {
  padding: 24px;
}

.profile-card {
  margin-bottom: 24px;
  text-align: center;
}

.profile-title {
  font-size: 24px;
  font-weight: bold;
}

.profile-table {
  margin-top: 16px;
  width: 100%;
}

.profile-table td {
  padding: 8px 0;
}


.cuti-card {
  padding: 24px;
}

.headline {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
}

.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.submit-container {
  display: flex;
  justify-content: center;
  margin-top: 16px;
}

.headline {
  display: flex;
  justify-content: center;
}
</style>
