<template>
    <v-container fluid>
      <v-app-bar app class="indigo" dark>
        <v-card-title class="headline">
          <v-btn icon @click="goBack">
            <v-icon>mdi-arrow-left</v-icon>
          </v-btn>
          <img src="/favicon.ico" alt="Logo" class="logo-small">
          Dovepin
        </v-card-title>
        <v-spacer></v-spacer>
      </v-app-bar>  
      <v-container class="content-container">
        <v-card class="cuti-history-card">
          <v-card-title class="headline">History Permohonan Cuti</v-card-title>
          <v-card-text>
            <v-data-table :headers="headers" :items="cutiList" hide-default-footer>
              <template v-slot:item.no="{ item }">
                {{ item.no }}
              </template>
              <template v-slot:item.statusCuti="{ item }">
                <span v-if="item.statusCuti === 'Menunggu'" class="status-menunggu">{{ item.statusCuti }}</span>
                <span v-if="item.statusCuti === 'Disetujui'" class="status-disetujui">{{ item.statusCuti }}</span>
                <span v-if="item.statusCuti === 'Ditolak'" class="status-ditolak">{{ item.statusCuti }}</span>
              </template>
              <template v-slot:item.action="{ item }">
                <v-icon
                  v-if="item.statusCuti === 'Menunggu'"
                  class="icon-approve"
                  @click="updateStatusCuti(item.id, 'Disetujui')"
                >
                  mdi-check-circle-outline
                </v-icon>
                <v-icon
                  v-if="item.statusCuti === 'Menunggu'"
                  class="icon-reject"
                  @click="updateStatusCuti(item.id, 'Ditolak')"
                >
                  mdi-close-circle-outline
                </v-icon>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-container>
    </v-container>
  </template>
  
  <script>
  import firebase from "firebase/app";
  import "firebase/auth";
  import { db } from "@/main";
  import Swal from "sweetalert2";
  
  export default {
    name: "ListCutiView",
    data() {
      return {
        cutiList: [],
        headers: [
          { text: "No", value: "no" },
          { text: "Nama (displayName)", value: "displayName" },
          { text: "Jenis Kelamin", value: "jenisKelamin" },
          { text: "Jabatan", value: "jabatan" },
          { text: "Tanggal Cuti", value: "tanggalCuti" },
          { text: "Lama Cuti (hari)", value: "lamaCuti" },
          { text: "Keterangan", value: "keterangan" },
          { text: "Status Cuti", value: "statusCuti" },
          { text: "Action", value: "action" },
        ],
      };
    },
    created() {
      const user = firebase.auth().currentUser;
      if (user) {
        this.getCutiList();
      } else {
        console.error("User not logged in.");
        this.$router.push("/home");
      }
    },
    methods: {
      getCutiList() {
        db.collection("cutis")
          .orderBy("tanggalCuti", "desc")
          .get()
          .then((querySnapshot) => {
            const cutiList = [];
            let counter = 1; // Inisialisasi counter
  
            querySnapshot.forEach((doc) => {
              const cuti = doc.data();
              cuti.no = counter++; // Tambahkan nomor dan naikkan counter
              cutiList.push({ id: doc.id, ...cuti });
            });
  
            this.cutiList = cutiList;
          })
          .catch((error) => {
            console.error("Error getting cuti list:", error);
            Swal.fire({
              icon: "error",
              title: "Error",
              text: "Terjadi kesalahan saat mengambil data cuti",
            });
          });
      },
      updateStatusCuti(cutiId, status) {
        db.collection("cutis")
          .doc(cutiId)
          .update({ statusCuti: status })
          .then(() => {
            Swal.fire({
              icon: "success",
              title: "Sukses",
              text: "Status Cuti Berhasil Diperbarui",
            });
            this.getCutiList();
          })
          .catch((error) => {
            console.error("Error updating status cuti:", error);
            Swal.fire({
              icon: "error",
              title: "Error",
              text: "Terjadi kesalahan saat memperbarui status cuti",
            });
          });
      },
      goBack() {
        this.$router.push("/dashboard");
      },
    },
  };
  </script>
  
  <style scoped>
  .logo-small {
    width: 50px;
    height: 50px;
    display: block;
    margin: 0 auto;
  }
  
  .content-container {
    padding: 24px;
  }
  
  .headline {
    margin-bottom: 16px;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
  }
  
  .cuti-history-card {
    padding: 24px;
  }
  
  .status-menunggu {
    color: orange;
    font-weight: bold;
  }
  
  .status-disetujui {
    color: green;
    font-weight: bold;
  }
  
  .status-ditolak {
    color: red;
    font-weight: bold;
  }
  
  .icon-approve {
    color: green;
    cursor: pointer;
  }
  
  .icon-reject {
    color: red;
    cursor: pointer;
  }
  </style>
  